import axios from "axios"
const name = window.localStorage.getItem("userName")
export default {
	data() {
		return {
			personal: [],
			name,
			hasPic: false
		}
	},
	methods: {},
	mounted() {
		axios
			.get("http://121.42.177.97:8080/red/use/pic_id.do?user_id=" + this.name)
			.then(res => {
				this.hasPic = true
			})
			.catch(res => {
				throw res
			})
		//获得个人昵称
		axios
			.post(
				"http://121.42.177.97:8080/red/user/findfriend.do?name=" + this.name
			)
			.then(response => {
				this.personal = response.data.data
			})
			.catch(function(error) {
				console.log(error)
			})
	}
}



